  watch:{
    inputSearchText:{
      handler:function(val){
        console.log(99);
        let data = {
          key: "RB5BZ-JSERU-SNBVG-4WPUM-5WWK5-O6FXZ",
          region: "",
          region_fix: 1,
          keyword: this.inputSearchText,
        };
        let url="https://apis.map.qq.com/ws/place/v1/suggestion";
        data.output="jsonp";
        $.ajax({
            type:"get",
            dataType:'jsonp',
            data:data,
            jsonp:"callback",
            jsonpCallback:"QQmap",
            url:url,
            success: searchData=> {
              console.log('mapdata',searchData);
              // console.log("searchData..",searchData);
              this.searchAddressList = searchData.data
              if(this.searchAddressList && this.searchAddressList.length>0){
                this.showList = true;
              }
              if(this.inputSearchText == ''){
                this.showList = false;
              }
            },
            error : err=> {
              this.showList = false;
              this.searchAddressList = [];
            }
        });
      },
      deep:true
    }
  },
  methods: {
    init(){
      window.parent.onscroll = (e)=>{
        this.scrollMsg();
      }
      this.id=this.$route.query.id;
      //ID
      if(this.id!=""){
        axios.get('/building-record/update?id='+this.id).then((res)=>{
          const initData =res.data;
          console.log("initData",initData);
          if(initData.error_code== 0){
              this.render(initData.data);
              this.form.showInfo=1;
          }else{
            this.alertMsg(initData.msg);
            return false;
          }
        }).catch((error)=>{
             console.log("error..",error);
        });
      }else{
        this.setDefaultType();
        this.form.showInfo=1;
      }
    },
    //
    checkUser(){
      axios.get('/building-record/create').then((res)=>{
        console.log("res",res);
        const initData =res.data;
        console.log("init",initData);
        if(initData.error_code== 0){
          this.form.buildTypeList=initData.data.buildTypeList;//
          this.creatorID=initData.data.user_id;//
          this.orgID=initData.data.org_id;//
          this.init();
        }else{
          this.alertMsg(initData.msg);
          return false;
        }
      }).catch((error)=>{
           console.log("error..",error);
      });
    },
    searchMap() {
      console.log("search")
      this.mapShow = true;
      this.inputSearchText = this.inputSearchVal;
    },
    closeMap() {
      this.mapShow = false;
    },
